<template>
    <pdd-layout
        class="layout_goods"
        page="goods"
        :bottom-text="bottomText">
        <div class="mall">
            <van-list
                v-if="listShowed.length"
                v-model="loading"
                :finished="finished"
                finished-text=""
                @load="loadMore"
            >
                <Commodity
                    v-for="(item, i) in listShowed"
                    :key="item.goodsId"
                    :commodity="item"
                    :index="i + 1"
                />
            </van-list>
        </div>
    </pdd-layout>
</template>

<script>
import { searchTopList } from '@/api/pddMall';
import PDDLayout from './components/PDDLayout';
import Commodity from './components/Commodity';

export default {
    name: 'PDDMallGoods',
    components: {
        pddLayout: PDDLayout,
        Commodity,
    },
    data() {
        return {
            listShowed: [],
            list: [],
            loading: false,
            finished: false,
            bottomText: '',
        };
    },
    created() {
        searchTopList({
            page: 1,
            pageSize: 300,
        }).then(
            (ret) => {
                this.list = ret.filter((v => v.discount));
                this.listShowed.push(...(this.list.slice(0, 10)));
                this.$root.hidePlaceholder();
                this.$o2o({
                    e_c: '热销首页',
                    e_a: '曝光',
                    e_n: 'PDD热销商品页',
                });
            },
            this.$error,
        );
    },
    methods: {
        loadMore() {
            if (this.list.length > this.listShowed.length) {
                this.listShowed.push(...(this.list.slice(this.listShowed.length,
                    this.listShowed.length + 10)));
            } else {
                this.bottomText = '- 今日爆款好物都在上面啦 -';
                this.finished = true;
            }
            this.loading = false;
        },
    },
};
</script>

<style scoped>
.layout_goods {
    height: 100vh;
}

.mall {
    padding: 127px 0 20px;
    min-height: 100%;
    border-top: 1px solid rgba(255, 1, 86, 1);
    background-color: rgba(255, 1, 86, 1);
    font-size: 16px;
}
</style>
