<template>
    <div
        class="commodity"
        @click="gotoDetail(commodity.goodsId, commodity.category)"
    >
        <img
            class="commodity__cover"
            :src="commodity.imgUrl">
        <div class="commodity__info">
            <div class="commodity__title">{{ commodity.name }}</div>
            <div class="commodity__toast">
                <div class="commodity__coupon">
                    <span class="commodity__icon">省</span>{{ commodity.discount }}元券
                </div>
                已售{{ commodity.salesTip }}件
            </div>
            <div class="commodity__footer">
                <div class="commodity__text">内部价</div>
                <div class="commodity__price">
                    <span>¥</span>{{ (commodity.originalPrice - commodity.discount).toFixed(1) }}
                </div>
                <div class="commodity__origin">原价<s>¥{{ commodity.originalPrice }}</s></div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    props: {
        commodity: {
            type: Object,
            required: true,
        },
        index: {
            type: Number,
            required: true,
        },
    },
    methods: {
        gotoDetail(id, category) {
            this.$o2o({
                e_c: '热销首页',
                e_a: '点击',
                e_n: 'PDD热销商品页',
                e_x: {
                    path: `第${parseInt((this.index / 10) + 1, 10)}屏`,
                    category,
                },
            });
            this.$router.push({
                name: 'pddMallGoodsDetail',
                query: {
                    goodsId: id,
                },
            });
        },
    },
};
</script>

